/* ===========================================
                Basic Styles
============================================ */
* {
  box-sizing: border-box; }

html, body {
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  font-weight: 300; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.clearfix:before,
.clearfix:before {
  display: table;
  content: " "; }

.clearfix:after {
  clear: both; }

.container, .inner {
  margin: 0 auto;
  width: 85%; }
  @media (min-width: 700px) {
    .container, .inner {
      width: 90%; } }

.alert, .notice {
  text-align: center; }

.center, #masthead .inner {
  transform: translate(-50%, -50%);
  position: relative;
  left: 50%;
  top: 50%; }

.text-center, .title, .title-left, #masthead .inner, #what-we-do {
  text-align: center; }

.center-100 {
  width: 100%; }

.bold {
  font-weight: 700; }

.hidden {
  display: none; }

.title, .title-left {
  text-transform: uppercase;
  font-weight: bold;
  padding: 40px 0;
  font-size: 40px;
  color: #3E3E3E;
  margin: 0; }
  @media (min-width: 700px) {
    .title, .title-left {
      font-size: 60px; } }

.title-left {
  text-align: left; }

p {
  color: #666666;
  font-size: 16px;
  margin: 0; }

a {
  text-decoration: none;
  color: #666666; }

/* ===========================================
                  Buttons
============================================ */
.button, .button-primary, .button-secondary, .button-red, .button-border {
  transition: 700ms background ease-in-out;
  border-radius: 2px;
  padding: 15px 20px;
  height: 50px;
  border: 0; }

.button-primary {
  background: #9E67B3;
  color: #FFFFFF; }
  .button-primary:hover {
    background: #854d9a; }

.button-secondary {
  background: #2691CD;
  color: #FFFFFF; }
  .button-secondary:hover {
    background: #1e73a2; }

.button-red {
  background: #EA4049;
  color: #FFFFFF; }
  .button-red:hover {
    background: #df1823; }

.button-border {
  border: 2px solid #FFFFFF;
  padding: 10px 20px;
  color: #FFFFFF; }
  .button-border:hover {
    background: #FFFFFF; }

/* ===========================================
              Main Navigation
============================================ */
.topheader {
  background: #9E67B3;
  height: 6px; }

#main-navigation {
  min-height: 100px; }
  #main-navigation .navigation-left {
    padding: 17px 0;
    float: left; }
    #main-navigation .navigation-left img {
      height: 60px; }
  #main-navigation a#mobile-navigation i {
    display: block;
    color: #FFFFFF; }
  #main-navigation .navigation-right {
    padding: 37px 0;
    display: none;
    float: right; }
    #main-navigation .navigation-right a {
      margin-right: 15px;
      color: #FFFFFF; }
      #main-navigation .navigation-right a:hover {
        color: #3E3E3E; }
  @media (min-width: 700px) {
    #main-navigation .navigation-right {
      display: block; }
    #main-navigation a#mobile-navigation {
      display: none; } }

/* ===========================================
                    Forms
============================================ */
input[type="text"],
input[type="email"],
input[type="number"],
input[type="password"],
textarea {
  border: 2px solid #D7D1D1;
  margin: .5em 0 2em 0;
  max-width: 100%;
  padding: 1em;
  width: 100%;
  height: 3.5em; }

input[type='number'] {
  height: 3.5em; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

textarea {
  max-height: 20em;
  resize: none;
  height: 20em; }

input[type="submit"] {
  border: 2px solid #D7D1D1;
  background: transparent;
  text-decoration: none;
  text-align: center;
  color: #666666;
  padding: 0 2em;
  font-size: 1em;
  outline: none; }
  input[type="submit"]:hover {
    border: 2px solid #9E67B3;
    color: #9E67B3; }

/* ===========================================
                  Masthead
============================================ */
#masthead {
  background: url("https://images.unsplash.com/photo-1423655156442-ccc11daa4e99?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&s=460fc65c8079dc4bd4a2ee7fa1fd7946") no-repeat;
  box-shadow: inset 0 0 0 1000px rgba(158, 103, 179, 0.6);
  background-size: cover;
  margin-top: -122px;
  z-index: -999;
  height: 900px; }
  #masthead .inner {
    margin: 0; }
    #masthead .inner h1, #masthead .inner h4 {
      color: #FFFFFF; }
    #masthead .inner .button-group {
      padding: 30px 0; }
      #masthead .inner .button-group .grid__col:nth-child(1) {
        padding: 0 0 45px 0; }
      @media (min-width: 700px) {
        #masthead .inner .button-group {
